.content {
   padding: 60px 0 0;
   @include md {
      padding: 22px 0 0; }
   @include sm {
      position: relative;
      padding: 58px 0 0; }

   &__container {
      padding: 0 24px 0 52px;
      @include lg {
         padding: 0 60px 0 52px; } }

   &__title {
      margin-bottom: 44px;
      @include lg {
         margin-bottom: 22px; }
      @include md {
         margin-bottom: 18px; }
      @include sm {
         display: none; } }

   &__inner {
      display: flex;
      margin: 0 -11px;
      @include lg {
         margin: 0 -9px; }
      @include md {
         margin: 0 -8px; }
      @include sm {
         margin: 0; }
      &-wrap {
         @include sm {
            max-width: 516px;
            margin: 0 auto; } }

      &.swiper-wrapper {
         @include rmin(767) {
            position: static;
            width: auto;
            height: auto;
            transform: none;
            box-sizing: border-box;
            transition: none; } } }

   &__col {
      padding: 0 11px;
      flex-grow: 0;
      flex-shrink: 1;
      @include lg {
         padding: 0 9px;
         flex-shrink: 0; }
      @include md {
         padding: 0 8px; }
      @include sm {
         padding: 0;
         flex-shrink: 0; }

      &_categories {
         width: percentage(906 / 1364);
         @include lg {
            width: percentage(815 / 1168); }
         @include md {
            width: percentage(467 / 744); }
         @include sm {
            flex-basis: 300px;
            width: 300px;
            margin-right: 13px; } }
      &_side {
         width: percentage(479 / 1364);
         @include lg {
            width: percentage(382 / 1168); }
         @include md {
            width: percentage(278 / 744); }
         @include sm {
            flex-basis: 300px;
            width: 300px; } } }

   &__aside-block {
      visibility: hidden;
      border: 1px solid;
      border-radius: 22px;
      box-shadow: 13px 7px 30px rgba($black, .21);
      @include md {
         border-radius: 10px; }
      &:not(:last-child) {
         margin-bottom: 30px;
         @include md {
            margin-bottom: 24px; } } }

   &__nav {
      display: none;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      justify-content: center;
      align-items: center;
      padding: 6px 0;
      transition: .15s ease-in-out;
      z-index: 101;
      background-color: transparent;
      will-change: background-color;
      @include sm {
         display: flex; }
      &.is-fixed {
         position: fixed;
         box-shadow: 0 3px 25px 3px rgba($black, .36);
         background-color: $white; }

      &-btn {
         position: relative;
         padding: 2px 24px 5px;
         border-radius: 50px;
         background: 0;
         @include f('hel700', 17);
         color: #69686A;
         transition: .5s ease-in-out;
         white-space: nowrap;
         will-change: left, right;
         &.is-active {
            background-color: $white;
            color: #0CC245;
            box-shadow: 7px 13px 30px rgba($black, .21); }
         .is-fixed &.is-active & {
            box-shadow: 3px 5px 5px rgba($black, .21); }
         &:nth-child(1) {
            @include r(424) {
               left: -15%; }
            @include r(374) {
               left: -26%; }
            &.is-active {
               @include r(424) {
                  left: 10%; }
               @include r(374) {
                  left: 20%; } } }
         &:nth-child(2) {
            @include r(424) {
               right: -26%; }
            &.is-active {
               @include r(424) {
                  right: 10%; }
               @include r(374) {
                  right: 20%; } } } } } }
